html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
  font-family: "PT Sans", Calibri, Tahoma, sans-serif;
}
body {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
ul,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}
li {
  list-style: none;
}

:root {
  /* HEADER */
  --header-height_fullscreen: 70px;
  --header-height_phonescreen: 200px;
  --header-height-fullscreen: 200px;
  --skills-top-phonescreen: 70px;
}

* {
  scroll-behavior: smooth;
}

.AboutContainer {
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  background-image: linear-gradient(to right, black 50%, gray);
  display: flex;
  align-items: center;
  padding: 20px 25% 20px 7%;
  color: white;
}

.AboutContainer__text {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.AboutContainer__text h1 {
  font-size: 90px;
  margin-bottom: 30px;
  background-image: linear-gradient(
    to right,
    red,
    orange,
    yellow,
    green,
    blue,
    indigo,
    violet
  );
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}

.AboutContainer__text > p {
  font-size: 32px;
  margin-bottom: 30px;
  line-height: 35px;
  width: 100%;
}

@media screen and (max-width: 1200px) {
  .AboutContainer {
    background-image: linear-gradient(to right, black 80%, gray);
    padding: 0 10% 0 7%;
  }
}

@media screen and (max-width: 900px) {
}

@media screen and (max-width: 650px) {
  .AboutContainer__text h1 {
    font-size: 36px;
    margin-bottom: 5px;
  }

  .AboutContainer__text p {
    font-size: 20px;
    margin-bottom: 10px;
  }
}

.ContactContainer {
  background-image: url(/Portfolio/static/media/contact.eaa1041d.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 70vh;
  padding: 20px;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  transition: all 1s ease-in-out;
}

.ContactContainer > h1{
  color: white;
}

.emailMe {
  display: flex;
  flex-direction: column;
  width: 500px;
  grid-gap: 15px;
  gap: 15px;
}

.emailMe > input {
  border: none;
  outline: none;
  height: 2rem;
  font-size: 1.5rem;
  border-radius: 5px;
  background-color: rgba(250, 250, 250, 0.8);
  
}

.emailMe > input:focus {
  background-color: white;
}

.emailMe > .message {
  height: 200px;
  border: none;
  outline: none;
  font-size: 1.5rem;
  border-radius: 5px;
  background-color: rgba(250, 250, 250, 0.8);
}

.emailMe > .message:focus {
  background-color: white;
}

.submit {
  height: 35px;
  font-size: 1.5rem;
  width: 50%;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
}
.submit:hover {
  background-color: #da6c60;
  cursor: pointer; 
  box-shadow: 4px 5px 15px 1px #000000;
}

@media only screen and (max-width: 1250px) {
  .ContactContainer {
    background-image: url(/Portfolio/static/media/contact-phone.72226346.jpg);
    align-items: center;
  }
  .social_icon {
    color: black;
  }

}

@media only screen and (max-width: 900px) {
  .ContactContainer {
    align-items: center;
  }
}


@media only screen and (max-width: 700px) {
  .emailMe {
    width: 100%;
  }
}
.FooterContainer {
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  background-image: linear-gradient(to right, #223442 50%, #4f3029);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
}

ul {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}
li a {
  position: relative;
  display: flex;
  align-items: center;
}

.social_icon {
  font-size: 30px;
  cursor: pointer;
  color: white;
}

.social_name {
  position: absolute;
  bottom: 5px;
  left: 50px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.6);
  font-size: 25px;
  opacity: 0;
  letter-spacing: 0.8px;
  pointer-events: none;
  z-index: 2;
  padding: 4px 5px;
  width: -webkit-max-content;
  width: max-content;
  transition: all 0.5s ease;
}

li a:hover .social_name {
  opacity: 1;
}

.FooterContainer > span {
  font-size: 24px;
  color: white;
}

.Header_Container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 10px;
  height: var(--header-height_fullscreen);
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: height 0.5s ease-in-out;
  z-index: 999;
}

.Profile_Content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: -webkit-max-content;
  width: max-content;
}

.Profile_Img_Container {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.Profile_Img_Container img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: -webkit-transform 0.4s ease;
  transition: transform 0.4s ease;
  transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.Profile_Img_Container img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.Profile_Name {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  width: -webkit-max-content;
  width: max-content;
  color: rgb(250, 250, 250);
}

.Header_Container ul {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  width: -webkit-max-content;
  width: max-content;
  color: white;
  font-size: 20px;
}

ul li {
  font-family: "Times New Roman", Times, serif;
  position: relative;
  padding: 5px;
  display: inline-block;
}
/* ANIMATION */
ul li::after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

ul li:hover {
  cursor: pointer;
}

ul li:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}
/*  */

@media only screen and (max-width: 700px) {
  .Header_Container {
    height: var(--header-height_phonescreen);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

