:root {
  --borderRadius: 12px;
}
.ProjectsContainer {
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
  padding: 30px 10px 70px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(200, 200, 200, 0.6);
  transition: all 1s;
}

.ProjectsContainer h1 {
  margin-bottom: 20px;
}

.Projects {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 30px;
  gap: 30px;
}

.ProjectCard {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  height: 400px;
}

.Project_Wrapper {
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  border-radius: 12px;
  border-radius: var(--borderRadius);
  width: 100%;
  height: 100%;
  grid-gap: 10px;
  gap: 10px;
  transition: all 0.2s ease-in-out;
}
.Project_Wrapper:hover {
  scale: 1.05;
}
.Project_Wrapper p {
  font-size: 1.8rem;
}

.Project_Wrapper p:after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}
.Project_Wrapper p:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
}

.imgWrapper {
  width: 100%;
  height: 85%;
  display: flex;
}
.Project_Wrapper img {
  width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.Project_Eplain {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  background-color: rgb(241, 228, 204);
  padding: 5px;
  border-radius: 12px;
  border-radius: var(--borderRadius);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.ProjectCard:hover .Project_Eplain {
  visibility: visible;
  opacity: 1;
}

@media only screen and (max-width: 1100px) {
  .Projects {
    grid-gap: 5px;
    gap: 5px;
  }
  .ProjectCard {
    width: 400px;
    height: 350px;
  }
  .Project_Wrapper:hover {
    scale: 1.01;
  }
}

@media only screen and (max-width: 500px) {
  .ProjectCard {
    width: 350px;
    height: 300px;
  }
}
@media only screen and (max-width: 400px) {

}

.skills_container {
  margin-top: var(--header-height_fullscreen);
  transition: margin-top 1s;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
ul {
  width: 95%;
  height: -webkit-max-content;
  height: max-content;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}
ul li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  padding: 5px;
  font-size: 1.5rem;
}

ul img {
  width: 60px;
}
.React_Icon {
  font-size: 30px;
}

.skill_separator {
  width: 5px;
  height: 5px;
  margin: 0px 2px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
}

span {
  pointer-events: none;
}
ul > li,
.React_Icon {
  -webkit-animation: bounce;
          animation: bounce;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@media only screen and (max-width: 700px) {
  ul img {
    width: 50px;
  }
  .React_Icon {
    font-size: 30px;
  }
  .skills_container {
    margin-top: var(--header-height-fullscreen);
  }
}

